.button{
  position: relative;
  min-width: 180px;
  top: 0;
  border: none;
  background: linear-gradient(to right, #828fd3 0%, #5e6dbe 50%, #5461aa 100%);
  color: white;
  border-radius: 27px;
  padding: 14px 22px;
  margin-right: 24px;
  margin-bottom: 12px;
}

.button:hover{
  top: -4px;
  transition: 300ms;
  box-shadow: 0 12px 34px 0 rgba(0, 0, 0, 0.21);
}

.quoteWrapper{
  text-justify: distribute;
  width: fit-content;
  max-width: 70%;
}

.quoteTitle{
  color: #FFF;
  font-size: 32px;
}

.quote{
  color: #FFF;
  font-size: 32px;
  font-style: italic;
  margin: 40px 0 0 0;
}

.quoteAuthor{
  margin-top: 12px;
  text-align: start;
  font-size: 32px;
  color: #FFF;
  font-style: oblique;
}